import React, { Component } from 'react';
import { Row, Form, Col, Select } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { setTimesEnteringProcessScore } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommon extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Chọn lớp tín chỉ trong kỳ:">
              {getFieldDecorator('chonloptinchitrongky', {})(
                <Select>
                  {setTimesEnteringProcessScore.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormInfoCommon;



