import React, { Component, Fragment } from 'react';
import { Table, Checkbox, Input } from 'antd';
import { setStudentForEnteringProcessScore } from 'dump/data';
import { BtnDelete } from 'components/common/button';

class TableEnteringProcessScore extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã sinh viên',
        dataIndex: 'masv',
        key: 'masv',
      },
      {
        title: 'Họ và tên',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Tên lớp',
        dataIndex: 'className',
        key: 'className',
      },
      {
        title: 'Số tiết nghỉ',
        dataIndex: 'sotietnghi',
        key: 'sotietnghi',
      },
      {
        title: 'Thiếu bài TH',
        dataIndex: 'thieu_bai_TH',
        render: () =>
          setStudentForEnteringProcessScore.length >= 1 ? (
            <Fragment>
              <Checkbox />
            </Fragment>
          ) : null,
      },
      {
        title: 'CC (10)',
        dataIndex: 'cc10',
        render: () =>
          setStudentForEnteringProcessScore.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'KT1 (30)',
        dataIndex: 'KT_30',
        render: () =>
          setStudentForEnteringProcessScore.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'Điểm TH',
        dataIndex: 'diemTH',
        render: () =>
          setStudentForEnteringProcessScore.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'Ghi chú',
        dataIndex: 'note',
        key: 'note',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: () => (
          <BtnDelete type="error" className="btnSmallAddInTable" onClick={this.showModal}>
            Xóa
          </BtnDelete>
        ),
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <p>
          <Checkbox /> Chọn mặc định điểm chuyên cần (Điểm 10)
        </p>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setStudentForEnteringProcessScore}
          columns={this.columns}
        />
      </div>
    );
  }
}

export default TableEnteringProcessScore;



