import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
            <Menu.Item key="save">
              <a href="#/">
                <Icon type="save" theme="twoTone" twoToneColor="#6699ff" />
                Lưu
              </a>
            </Menu.Item>
            <Menu.Item key="blockScore">
              <a href="#/">
                <Icon type="save" theme="twoTone" twoToneColor="#ff3333" />
                Khóa điểm
              </a>
            </Menu.Item>
            <Menu.Item key="delete">
              <a href="#/">
                <Icon type="delete" theme="twoTone" twoToneColor="#ff3333" />
                Xóa tất cả
              </a>
            </Menu.Item>
            <Menu.Item key="typeScore">
              <a href="#/student/enteringTypeScore">
                <Icon type="delete" theme="twoTone" twoToneColor="#ff3333" />
                Thành phần điểm
              </a>
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
              Xuất Excel
            </Menu.Item>
            <Menu.Item key="printReport">
              <Icon type="printer" theme="twoTone" twoToneColor="#6699ff" />
              In báo cáo
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



